html {
    // This defines what 1rem is
    font-size: 62.5%; //1 rem = 10px; 10px/16px = 62.5%

    @include respond(tab-land) { // Width > 1200px
        font-size: 56.25%; // 1rem 9px, 9/16 = 56.25%
    }
    @include respond(phone) { // Width < 900px
        font-size: 50%; // 1rem 8px, 8/16 = 50%
    }
    scroll-behavior: smooth;
}

body{
    font-size: 1.6rem;
    font-family:$main-font;
    background-image: url('../img/bg.png');
    background-color: $maincolor-dark;
}

*{
    font-family: inherit;
    //transition: all .4s ease-in-out;
}

//loading scren
.loading {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #2c2a2f;
    z-index: 99999;
    display: flex;
    justify-content: center;
    justify-items: center;
    align-content: center;
    align-items: center;
    transition: all .4s ease-in-out;
}

.loading .sk-folding-cube {
    width: 6rem;
    height: 6rem;
    position: relative;
    transform: rotateZ(45deg);
}

.loading .sk-folding-cube .sk-cube {
    float: left;
    width: 50%;
    height: 50%;
    position: relative;
    transform: scale(1.1);
}
.loading .sk-folding-cube .sk-cube:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #f5865d;
    animation: sk-foldCubeAngle 2.4s infinite linear both;
    transform-origin: 100% 100%;
}

.loading .sk-folding-cube .sk-cube2 {
    transform: scale(1.1) rotateZ(90deg);
}

.loading .sk-folding-cube .sk-cube3 {
    transform: scale(1.1) rotateZ(180deg);
}

.loading .sk-folding-cube .sk-cube4 {
    transform: scale(1.1) rotateZ(270deg);
}

.loading .sk-folding-cube .sk-cube2:before {
    animation-delay: 0.3s;
}

.loading .sk-folding-cube .sk-cube3:before {
    animation-delay: 0.6s;
}

.loading .sk-folding-cube .sk-cube4:before {
    animation-delay: 0.9s;
}

@-webkit-keyframes sk-foldCubeAngle {
    0%, 10% {
        -webkit-transform: perspective(140px) rotateX(-180deg);
        transform: perspective(140px) rotateX(-180deg);
        opacity: 0;
    }
    25%, 75% {
        -webkit-transform: perspective(140px) rotateX(0deg);
        transform: perspective(140px) rotateX(0deg);
        opacity: 1;
    }
    90%, 100% {
        -webkit-transform: perspective(140px) rotateY(180deg);
        transform: perspective(140px) rotateY(180deg);
        opacity: 0;
    }
}

@keyframes sk-foldCubeAngle {
    0%, 10% {
        -webkit-transform: perspective(140px) rotateX(-180deg);
        transform: perspective(140px) rotateX(-180deg);
        opacity: 0;
    }
    25%, 75% {
        -webkit-transform: perspective(140px) rotateX(0deg);
        transform: perspective(140px) rotateX(0deg);
        opacity: 1;
    }
    90%, 100% {
        -webkit-transform: perspective(140px) rotateY(180deg);
        transform: perspective(140px) rotateY(180deg);
        opacity: 0;
    }
}


.mix{
    transition: none;
}
.min-titel{
    font-family: $titel-font;
    color: $maincolor;
    position: relative;
    margin-bottom: 6rem;
    h2{
        padding-left: 4.5rem;
        padding-top: .8rem;
        font-size: 2rem;
        text-transform: uppercase;
        font-weight: 600;
        margin: 0;
        &::before{
            content: '';
            position: absolute;
            top: 2rem;
            left: 17rem;
            width: 6rem;
            border-top:.2rem dotted $color-titel; 
        }
        &::after{
            content: '';
            position: absolute;
            top: 1.5rem;
            left: 23rem;
            width: .5rem;
            height: .5rem;
            border-radius: 50%;
            border:.3rem solid $color-titel;
            background-color: $maincolor;
            box-shadow: 0 0 1rem rgba(0, 0, 0, .2);
            box-sizing: content-box;
        }
    }
    .holder{
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        width: 9.5rem;
        height: 4rem;
        border: .1rem dotted $color-titel;
        background: $color-grey;
        border-right: none;
        z-index: -1;
    }
    .num{
        position: absolute;
        left: 25rem;
        bottom: .3rem;
        color: $color-titel;
        font-size: 1.7rem;
        font-weight: 800;

    }
}
::-webkit-scrollbar-thumb {
    border-radius: .8rem;
    background-color: $maincolor;

}
::-webkit-scrollbar{
    background-color: $color-grey;
    width: .3rem;
}
::selection {
    background: $maincolor;
    color: #fff;
}

button{
    cursor: pointer;
    font-size: 1.6rem;
    outline: none !important;
}
h1, h2, h3, h4, h5, h6{
    font-family: $titel-font;
}
p, h2{
    line-height: 1.6;

    @include respond(0){
        line-height: 1.3;
    }
}

ul{
    padding: 0;
    margin: 0;
    list-style: none;
}
a, a:hover, a:focus, a:active {
    color: #fff;
    text-decoration: none;
}

.svg{
    width: 1.5rem;
    height: 1.5rem;
}